// CONSTANTS
$background: #171819;
$border: #63676b;

$comment: #008800;
$comment_preproc: #ff0007; // Python docstrings, for instance

$text: #ffffff;
$text_attribute: #ff0007;
$text_highlight: #fb660a;
$text_literal: #0086d2;
$text_name: #ff0007;
$text_number: #0086d2;
$text_output: #444444;
$text_type: #cdcaa9;
$text_whitespace: #888888;

// INLINE CODE
li > code,
p > code {
    background-color: $background;
    border: 1px solid $border;
    border-radius: 4px;
    color: $text_type;
    font-family: "Roboto Mono", sans-serif;
    padding: 0 0.25em; }

// CODE BLOCK THEME STYLES
div.highlight {
    background-color: $background;
    border: 1px solid $border;
    border-radius: 4px;
    font-family: "Roboto Mono", sans-serif;
    font-size: 0.6em;
    line-height: 1.2em;
    margin: 1.5em 0;
    overflow-x: auto;
    padding: 1em; }

@media (max-width: 960px) {
    div.highlight {
        font-size: 0.8em; } }

// NECESSARY CUSTOM ADDITIONS
// Plaintext (placed in a `code` block)
.highlight code {
    color: $text; }

// Literal String Quote
.highlight .dl {
    color: $text_literal; }

// Literal String Colon
.highlight .pi {
    color: $text; }

.highlight .hll {
    background-color: $background; }

